import React from 'react'
import '../styles/trandingSection.css'
import { Link } from 'gatsby'
import TrandingCard from '../components/TrandingCard'

export default function TrandingSection() {
    return (
        <section className="tranding-section">
            <div className="container-xxl px-5 px-xxl-0">
                <div className="row tranding-row">
                    <div className="col-4 tranding-news-col">
                        <h2>TRANDING NEWS</h2>
                        <Link to="/"><img src="/qkzclan/images/avengers.png" alt="avengers" /></Link>
                    </div>
                    <div className="col-3 tranding-card-col p-0">
                        <TrandingCard
                            imgSource='/qkzclan/images/action.png'
                            imgAlt='action'
                            titleColor='red'
                            cardTitle='ACTION'
                            cardText='THE GENERAL SERVICES ADMINISTRATION'
                        />
                        <TrandingCard
                            imgSource='/qkzclan/images/classic.png'
                            imgAlt='action'
                            titleColor='orange'
                            cardTitle='CLASSIC'
                            cardText='THE GENERAL SERVICES ADMINISTRATION'
                        />
                        <TrandingCard
                            imgSource='/qkzclan/images/3d-video.png'
                            imgAlt='action'
                            titleColor='blue'
                            cardTitle='3D VIDEO'
                            cardText='THE GENERAL SERVICES ADMINISTRATION'
                        />
                        <TrandingCard
                            imgSource='/qkzclan/images/porn.png'
                            imgAlt='action'
                            titleColor='pink'
                            cardTitle='PORN'
                            cardText='THE GENERAL SERVICES ADMINISTRATION'
                        />
                    </div>
                    <div className="col-5 top-games-col">
                        <h2>TOP GAMES</h2>
                        <div className="top-games">
                            <div className="row g-4 mb-4 text-center">
                                <div className="col-6">
                                    <Link to="/">
                                        <img src="/qkzclan/images/top-game1.png" alt="top game" />
                                    </Link>
                                </div>
                                <div className="col-6">
                                    <Link to="/">
                                        <img src="/qkzclan/images/top-game2.png" alt="top game" />
                                    </Link>
                                </div>
                            </div>
                            <div className="row g-4 text-center">
                                <div className="col-6">
                                    <Link to="/">
                                        <img src="/qkzclan/images/top-game3.png" alt="top game" />
                                    </Link>
                                </div>
                                <div className="col-6">
                                    <Link to="/">
                                        <img src="/qkzclan/images/top-game4.png" alt="top game" />
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}
