import React, {useState, useEffect} from 'react'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from 'react-slick';
import '../styles/videoSlider.css'

export default function VideoSlider() {
    const [slideNumber, setSlideNumber] = useState(3);
    useEffect(() => {
        function handleResize() {
            if (window.innerWidth < 1275) setSlideNumber(2)
            else setSlideNumber(3);
            if (window.innerWidth < 885) setSlideNumber(1);            
        }
        window.addEventListener("resize", handleResize);
        return () => window.removeEventListener("resize", handleResize);
    }, []);
    var settings = {
        infinite: true,
        speed: 500,
        slidesToShow: slideNumber,
        slidesToScroll: slideNumber,
        autoplay: true,
        arrows: true
    }
    return (
        <div className="video-slider-wrapper">
            <div className='container-xxl px-5 px-xxl-0'>
                <p>VIDEO NEWS</p>
                <Slider {...settings}>
                    <div className="video-slide-item">
                        <img src="/qkzclan/images/video-news1.png" alt="video news" />
                    </div>
                    <div className="video-slide-item">
                        <img src="/qkzclan/images/video-news2.png" alt="video news" />
                    </div>
                    <div className="video-slide-item">
                        <img src="/qkzclan/images/video-news3.png" alt="video news" />
                    </div>
                    <div className="video-slide-item">
                        <img src="/qkzclan/images/video-news1.png" alt="video news" />
                    </div>
                    <div className="video-slide-item">
                        <img src="/qkzclan/images/video-news2.png" alt="video news" />
                    </div>
                    <div className="video-slide-item">
                        <img src="/qkzclan/images/video-news3.png" alt="video news" />
                    </div>
                </Slider>
            </div>
        </div>
    )
}
